//move camera
editRoom.onclick = function(){moveCamera()}



function moveCamera(){
    if(controls.enabled == true)
    {
        
        flipControl()
        controls.enabled = false
        editRoom.innerText = 'Done Editing'
        gsap.to(camera.position, { duration: 1, delay: 0, x:0, y:4, z:0 })
        gsap.to(camera.rotation, { duration: 1, delay: 0,  x: -Math.PI * .5, y: 0, z: 0, })
        .then(dragControls.activate ())
        // new TWEEN.Tween( camera.position ).to(
        //     {
        //         x: 0,
        //         y: 4,
        //         z: 0
        //     }, 1200 )
        //     .easing(TWEEN.Easing.Quadratic.InOut)
        //     .onComplete(TWEEN.removeAll())
        //     .start();

        // new TWEEN.Tween( camera.rotation ).to(
        //     {
        //         x: -Math.PI * .5,
        //         y: 0,
        //         z: 0,
        //     }, 1200 )
        //     .easing(TWEEN.Easing.Quadratic.InOut)
        //     .onComplete(dragControls.activate ())
        //     .start();   
        
        controls.autoRotate = false
        controls.enableRotate = false
        controls.enableDamping = false
        
        camera.updateProjectionMatrix()
        render()
    }
    else if(controls.enabled == false)
    {
        // console.log('back', controls.enabled)

        dragControls.deactivate()
        editRoom.innerText = 'Edit Room'
        gsap.to(camera.position, { duration: 1, delay: 0, x:0, y:4, z:4 })
        gsap.to(camera.rotation, { duration: 1, delay: 0,  x: -1.0190506056799675, y: 0.8258247889094881, z: 0.873718057648819, })
        .then(
            flipControl(), 
            controls.enabled = true,
            controls.autoRotate = true,
            controls.enableRotate = true,
            // camera.position.set(4, 4, -4)
            controls.target.set(0, 0, 0)
            )
        
     
        // camera.position.set(4, 4, -4)

        
        // camera.updateProjectionMatrix()
        // render()
    }
    
    console.log('clicked')
}

